@import 'constants/constants.sass';

* {
  outline: none !important;
  font-family: Roboto, sans-serif;
  margin: 0;
  padding: 0;

  html::-webkit-scrollbar, body::-webkit-scrollbar {
    width: 0; } }

h1, h2, h3 {
  font-weight: 500; }

h2 {
  font-size: 20px;
  margin-bottom: 15px; }

input {
  font-size: 1.2rem; }

.background {
  background: url('img/bg.jpg');
  background-size: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1; }
.wrapper {
  max-width: 1300px;
  margin: 0 auto;
  transition: 1s; }
.app {
  position: relative;
  padding: 0 20px;
  margin-bottom: 20px;
  .timer-wrap {
    padding: 30px;
    flex-shrink: 0; }
  input {
    padding-left: 10px;
    font-size: 15px;
    font-weight: 400;
    color: #727070;
    letter-spacing: 0.16px;
    max-width: 400px;
    height: 25px;
    border: 1px solid #e7e5e5; }
  button {
    border: none;
    background: none;
    color: inherit;
    text-transform: inherit;
    font-size: inherit;
    letter-spacing: inherit;
    cursor: pointer; }
  button.ordinar {
    cursor: pointer;
    background-color: $light-green;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.24);
    border-radius: 30px;
    padding: 10px 40px;
    color: $white;
    border: none;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.42px;
    transition: .3s; }
  button.fiolet {
    background-color: $fiolet; }
  section {
    display: flex; }
  main, aside {
    display: flex;
    flex-direction: column; }
  main {
    width: 65%; }
  .no-padding {
    padding: 0; }
  .nav-tabs {
    margin-bottom: 10px;
    display: flex;
    .nav-item {
      flex-grow: 1;
      margin-top: 10px; } }
  .delete-wrap {
    position: relative;
    height: 50px;
    order: 0;
    &:hover .delete {
      opacity: 1; }
    .delete {
      width: 25px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $white;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      z-index: 10;
      cursor: pointer;
      opacity: 0;
      svg {
        fill: #000;
        height: 15px;
        width: 15px; } } }
  .react-tabs {
    .react-tabs__tab-list {
      border-bottom: none;
      background-color: #f5f5f5;
      .react-tabs__tab {
        padding: 20px;
        border: none; } } } }

.react-customscroll-scrollbar-area {
  background: none !important;
  .react-customscroll-scrollbar {
    background: rgba(174, 174, 174, 0.41) !important;
    &:hover {
      background: rgba(174, 174, 174, 0.81) !important; } } }

@media only screen and (max-width: 1250px) {
  .main-wrapper {
    flex-direction: column; }
  .main-nav {
    width: 100% !important; }
  main {
    width: 100% !important; }
  .h1-page {
    padding-top: 120px !important; }
  .timer-wrap {
    padding: 0 !important; }
  .seconds {
    font-size: 44px !important; }
  .react-timer__buttons {
    flex-wrap: wrap !important; }
  .react-timer__button {
    margin-bottom: 10px !important; }
  .form {
    flex-direction: column;
    input, .data-list-wrapper {
      width: calc(100% - 15px) !important;
      max-width: 100% !important; }
    button {
      margin-top: 10px;
      width: 100%; } } }
