@import '../../constants/constants.sass';

.add-event {
  padding: 20px;
  position: relative;
  &:after {
    content: '';
    display: block;
    position: absolute;
    width: 90%;
    height: 2px;
    background: white;
    bottom: 20px; }
  .react-customscroll-frame {
    display: flex;
    flex-direction: column; }
  .list {
    overflow: hidden;
    margin-top: 10px; }

  .list-item {
    display: flex;
    align-items: center;
    margin-right: 10px;
    padding: 10px 0;
    font-size: 14px;
    font-weight: 400;

    .nmb {
      margin-right: 5px; }

    &--error {
      color: $error; }

    &--check {
      color: $check; } }

  .checkbox__checked {
    color: $gray;
    order: 1;
    .scroller-text {
      text-decoration: line-through; } }
  .checkbox__checked--animate {
    animation: checked 1s ease-in-out; }
  .checkbox__nocheck--animate {
    animation: non-checked 1s ease-in-out; }

  .list-item__checklist {
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
    border-bottom: 1px solid #e0e0e0;

    .scroller-wrap {
      margin: 0 40px; }

    .check-sign {
      font-size: 0;
      position: absolute;
      left: 10px;
      width: 20px;
      height: 20px;
      border: 1px solid #bdbdbd;
      border-radius: 3px;
      margin-right: 10px; }

    .check-sign__checked {
      border: none;
      background: $fiolet;
      font-size: 15px;
      line-height: 3;
      color: $white;
      display: flex;
      justify-content: center;
      align-items: center; } }

  .list-item__mistakes {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 400;
    z-index: 1;

    .mistake-cnt {
      width: 30px;
      height: 30px;
      font-size: 15px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $fiolet;
      border: 1px solid $fiolet;
      margin-right: 20px;
      flex-shrink: 0;
      cursor: default;
      pointer-events: none; } } }

.add-event-mistake {
  padding-bottom: 10px;
  padding-top: 25px; }

@keyframes checked {
  0% {
    opacity: 0; }
  25% {
    opacity: 0; }
  50% {
    opacity: 0;
    height: 0; }
  75% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes non-checked {
  0% {
    opacity: 0; }
  25% {
    opacity: 0; }
  50% {
    opacity: 0;
    height: 0; }
  75% {
    opacity: 0;
    height: 0; }
  100% {
    opacity: 1;
    height: 50px; } }
